import React, { useState, useEffect, useContext } from 'react';

import { Nav, Dropdown } from "react-bootstrap"

import { Helmet } from 'react-helmet'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// faToggleOn, faToggleOff
import { LockRounded } from '@material-ui/icons';
import Axios from "axios"
import { getApiLink } from "../lib/env.lib"

import { faCaretRight, faCaretDown } from '@fortawesome/free-solid-svg-icons';
import { AuthContext } from "../contexts/auth_context"

import notyf from "../lib/notification"
export const ProfileSettings = (props) => {
    const { user_id, user_dp, user_fullname, user_username, user_bio, user_gender, auth_token, setAuth } = useContext(AuthContext);

    // const [state, setstate] = useState(initialState)
    const [u_dp, setDp] = useState(user_dp)
    const [u_name, setName] = useState(user_fullname)
    const [u_bio, setBio] = useState(user_bio)
    const [u_gender, setGender] = useState(user_gender)

    function handleChange(e) {
        if (e.target.name === "name") {
            setName(e.target.value)
        }

        if (e.target.name === "bio") {
            setBio(e.target.value)
        }

        if (e.target.name === "gender") {
            setGender(e.target.value)
        }
    }

    function saveImage(dp) {
        Axios.post(`${getApiLink()}/api/v1/updateUserSettings`, {
            "xAuthToken": auth_token,
            "profile_image_link": dp
        }).then((doc) => {
            console.log(doc)

            if (doc.data.success) {
                setDp(dp)
                doc.data.message.token = auth_token
                console.log({ doc })
                setAuth(doc.data)
                notyf.success("Successfully updated image")
            } else {
                notyf.error("Error occured while updating image")
            }
        }).catch((e) => {
            console.log(e)
            console.log("Error while uploading image")
            notyf.error("Error occured while updating image")
        })
    }

    function handleUpload() {

        let f_i = document.createElement("input")
        f_i.setAttribute("type", "file")
        f_i.setAttribute("accept", "image/*")

        f_i.click()

        f_i.addEventListener("change", () => {

            if (f_i.files[0]) {

                let f = new FormData()
                f.append("file", f_i.files[0])
                return Axios.post(`${getApiLink()}/api/v1/upload`, f, {

                }).then((doc) => {
                    console.log(doc)

                    if (doc.data.success) {
                        saveImage(`${getApiLink()}/file/${doc.data.file_name}`)
                    } else {
                        notyf.error("Error occured while updating image")
                    }
                }).catch((e) => {
                    console.log(e)
                    console.log("Error while uploading image")
                    return {
                        success: 0
                    }
                })
            } else {
                notyf.error("Invalid file")
            }

            f_i.removeEventListener("change", () => { })
        })



    }

    function handleSave() {

        if (u_name === null || u_name === "" || u_name.length > 60) {
            notyf.error("Invalid name")
        } else if (u_gender === null || u_gender === "Choose...") {
            notyf.error("Invalid gender")
        } else if (u_bio === null || u_bio === "" || u_bio.length > 160) {
            notyf.error("Invalid bio")
        } else {
            Axios.post(`${getApiLink()}/api/v1/updateUserSettings`, {
                "xAuthToken": auth_token,
                "fullname": u_name,
                "user_bio": u_bio,
                "gender": u_gender
            }).then((doc) => {
                console.log(doc)

                if (doc.data.success) {
                    doc.data.message.token = auth_token
                    console.log({ doc })
                    setAuth(doc.data)
                    notyf.success("Successfully updated profile")
                } else {
                    notyf.error("Error occured while updating profile information")
                }
            }).catch((e) => {
                console.log(e)
                console.log("Error while updating information")
                notyf.error("Error occured while updating profile information")
            })
        }


    }
    return (<>

        <div className={props.className}>
            <h3>Public Profile</h3>
            <hr className="light" />
            <div className="row">
                <div className="col-md-4 text-center show-xs hide-md">

                    <div className="position-relative text-center">
                        <img className="avatar_image" alt="img" name="dp"
                            src={u_dp} />

                        <Dropdown>
                            <Dropdown.Toggle className="position-absolute profile-edit-btn btn-sm px-2 py-1 right-50 bottom-0 ml-2 mb-2" variant="info" id="dropdown-basic2">
                                Edit
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <Dropdown.Item eventKey="1" onClick={handleUpload}>Upload Image</Dropdown.Item>
                                {/* <Dropdown.Item eventKey="2">Remove Image</Dropdown.Item> */}
                            </Dropdown.Menu>
                        </Dropdown>


                    </div>

                </div>
                <div className="col-md-8">
                    <h6>Name</h6>
                    <input type="text" className="form-control" name="name" placeholder="Full Name" value={u_name} onChange={handleChange} />
                    <p className="text-muted">
                        Your name may appear around Lattice where you contribute or are mentioned.
                        </p>
                    <br />
                    <h6>Username</h6>
                    <input type="text" className="form-control" placeholder="@username" value={user_username} disabled />
                    <p className="text-muted">
                        Your username may appear around Lattice where you contribute or are mentioned.
                        </p>

                    <h6>Bio</h6>
                    <textarea id="" cols="30" rows="3" className="form-control" name="bio"
                        placeholder="Enter you Profile Bio" value={u_bio} onChange={handleChange} ></textarea>

                    <hr className="darker" />
                    <h6>Gender</h6>
                    <select className="custom-select form-control" id="ig-gender" name="gender" onChange={handleChange} value={u_gender}>
                        <option defaultChecked>Choose...</option>
                        <option value="0">Male</option>
                        <option value="1">Female</option>
                        <option value="2">Others</option>
                        <option value="-1">Choose not to specify.</option>
                    </select>
                    <button className="btn btn-primary mt-2" onClick={handleSave}>Save</button>
                </div>
                <div className="col-md-4 text-left pl-2 hide-xs show-md">

                    <div className="position-relative">
                        <img className="avatar_image" alt="img"
                            src={u_dp} />

                        <Dropdown>
                            <Dropdown.Toggle className="position-absolute profile-edit-btn btn-sm px-2 py-1 left-0 bottom-0 ml-2 mb-2" variant="info" id="dropdown-basic">
                                Edit
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <Dropdown.Item eventKey="1" onClick={handleUpload}>Upload Image</Dropdown.Item>
                                {/* <Dropdown.Item eventKey="2">Remove Image</Dropdown.Item> */}
                            </Dropdown.Menu>
                        </Dropdown>
                    </div>

                </div>
            </div>


        </div>

    </>);
}

const AccountSettings = (props) => {
    return (<>
        <div className={props.className}>
            <h3>Account Settings</h3>
            <hr className="light" />

            <div className="row">
                <div className="col-md-8">
                    This is something
                </div>
            </div>
        </div>
    </>);
}

const SecuritySettings = (props) => {

    function handleSave() {
        console.log("saving new password")
    }
    return (
        <>
            <div className={props.className}>
                <h3>Security Settings</h3>
                <hr className="light" />
                <div className="row">
                    <div className="col-md-8">
                        <br />
                        <h5>Change password</h5>
                        <hr className="light" />
                        <h6>Old passworrd</h6>
                        <input type="password" className="form-control" placeholder="Enter you current password" />

                        <h6 className="mt-3">New password</h6>
                        <div className="row">
                            <div className="col-md-6 mb-2">
                                <input type="password" className="form-control" placeholder="Enter new password" />
                            </div>
                            <div className="col-md-6">
                                <input type="password" className="form-control" placeholder="Repeat new password" />
                            </div>
                        </div>
                        <button className="btn btn-primary mt-3" onClick={handleSave}>Change password</button>

                        <h5 className="mt-4 pt-3">Two-factor authentication</h5>
                        <hr className="light" />
                        <div className="text-center">

                            <span className="mt-4 mb-2 pt-4 pb-2"><LockRounded className="text-muted" style={{
                                fontSize: "48px"
                            }} /></span>
                            <h5>Two factor authentication is not enabled yet.</h5>
                            <p className="text-muted" style={{
                                fontSize: "14px"
                            }}>Two-factor authentication adds an additional layer of security to
                                your account by requiring more than just a password to log in.</p>

                            <button className="btn btn-success mt-4">Enable two-factor authentication</button>
                        </div>

                    </div>
                </div>
            </div>
        </>);
}

function handleChange(display) {
    if (display === 1) {
        return (<ProfileSettings className="mt-4 pb-4" />)
    } else if (display === 2) {
        return (<AccountSettings className="mt-4 pb-4" />)
    } else {
        return (<SecuritySettings className="mt-4 pb-4" />)
    }
}

function showToggle(c, showMenu) {

    if (c) {
        return (
            <span>
                {/* {(showMenu === 1) ? <FontAwesomeIcon icon={faToggleOn} className="toggle-icon" /> : <FontAwesomeIcon icon={faToggleOff} className="toggle-icon" />} */}
                {(showMenu === 1) ? <FontAwesomeIcon icon={faCaretDown} className="toggle-icon" /> : <FontAwesomeIcon icon={faCaretRight} className="toggle-icon" />}
            </span>
        )
    } else {
        return ""
    }

}
// export default ProfileSettings;
const Settings = () => {

    const [display, setdisplay] = useState(1);

    const [showMenu, setshowMenu] = useState(1);
    const [useToggle, setUseToggle] = useState(0);
    // eslint-disable-next-line no-restricted-globals
    const w = screen.availWidth;

    useEffect(() => {

        if (w <= 767) {
            setUseToggle(1)
            setshowMenu(0)
        }

    }, [w])

    function setStateProfile(e) {
        // console.log("1")
        setdisplay(1)
    }

    function setStateAccount(e) {
        // console.log("2")
        setdisplay(2)
    }

    function setStateSecurity(e) {
        // console.log("3")
        setdisplay(3)
    }

    function setSettingsToggler() {
        if (w <= 767) {
            setshowMenu((showMenu + 1) % 2)
        }
    }


    return (<>
        <Helmet>
            <meta charSet="utf-8" />
            <title>Settings - Devorzo</title>
            <link rel="canonical" href="https://devorzo.github.io/settings" />
        </Helmet>
        <div className="settings-section">
            <div className="container">

                <div className="row">


                    <div className="col-lg-3 mb-0 mt-3">

                        <h2 className="noselect cursor-pointer" onClick={setSettingsToggler}>Settings&nbsp;
                        {showToggle(useToggle, showMenu)}
                        </h2>

                        <div className={(showMenu === 0) ? "hide-sm hide-xs" : ""}>
                            <Nav variant="pills" defaultActiveKey="link-1" className="flex-column settings-nav">
                                <Nav.Item>
                                    <Nav.Link i={1} className="nav-btn" onClick={setStateProfile} eventKey="link-1">Profile Settings</Nav.Link>
                                </Nav.Item>
                                {/* <Nav.Item>
                                    <Nav.Link i={1} className="nav-btn" onClick={setStateAccount} eventKey="link-2">Account Settings</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link i={1} className="nav-btn" onClick={setStateSecurity} eventKey="link-3">Security Settings</Nav.Link>
                                </Nav.Item> */}
                            </Nav>
                        </div>


                    </div>

                    <div className="col-lg-9">
                        {handleChange(display)}
                        <br />
                        <br />
                        <br />
                    </div>

                </div>
            </div>
        </div >
    </>

    );
}

export default Settings;